* {
    margin: 0;
    padding: 0;
    font-family:'Times New Roman', Times, serif;
    list-style: none;
}

header {
    padding: 50px;
}

.app-body {
    display: grid;
    grid-auto-flow: column;
}

.student-filter-header {
    width: 100%;
    text-align: center;
    margin-top: 25px;
}

.student-filter-header h2 {
    display: inline-block;
}

.student-filter-list {
    margin: 25px;
    list-style: none;
    transition: 0.2s;
}

.student-filter-list li:hover {
    cursor: pointer;
    text-shadow: 1px 1px 3px rgba(81, 81, 81, 0.575);
    transition: 0.2s;
}

.student-filter b {
    margin: 10px;
}

hr {
    border: none;
    border-radius: 24px;
    background-color: mediumseagreen;
    height: 5px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.theme-color {
    background-color: mediumseagreen;
}

.theme-color-text {
    color: mediumseagreen;
}

header {
    color: white;
    text-align: center;
}

h1 {
    display: inline-block;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 0;
}

.all-students-header {
    margin: 25px;
}

.student-card {
    border: 2px solid mediumseagreen;
    margin: 25px;
}

.student-photo {
    width: 156px;
    height: 156px;
    border-radius: 8px;
    margin: 25px;
}

.student-top-section {
    display: flex;
}

.student-general {
    margin-top: 50px;
}

.student-toggle {
    margin-top: 25px;
}

.student-bottom-section {
    display: flex;
}

.student-bottom-section ul{
    margin: 15px;
}

.student-scores p {
    margin-top: 20px;
    margin-bottom: 20px;
}

.toggle-click {
    text-decoration: underline;
    cursor: pointer;
}